import React from 'react';
import ReactDOM from 'react-dom';
import store from './reducer/display'
import Ingridients from './components/ingridients'
import Recepies from './components/recepies'
import Tabs from './components/tabs'
import count1 from "./reducer/sum";
import {Provider} from 'react-redux';
import Geolocation from "./components/geo";

let d;
class App extends React.Component{
    constructor(props){
        super(props);
        this.state={
            ok : false,
            lat : '',
            lon : '',
            pl : '',
            mob : false
        };
        this.validate = this.validate.bind(this);
        this.newLoc = this.newLoc.bind(this);
        this.tet = this.tet.bind(this);
    }
    componentDidMount(){
            if( navigator.userAgent.match(/Android/i)
                || navigator.userAgent.match(/webOS/i)
                || navigator.userAgent.match(/iPhone/i)
                || navigator.userAgent.match(/iPad/i)
                || navigator.userAgent.match(/iPod/i)
                || navigator.userAgent.match(/BlackBerry/i)
                || navigator.userAgent.match(/Windows Phone/i)
            ){
                this.setState({
                    mob : true
                })
            }
            else {
                this.setState({
                    mob : false
                })
            }
    }
    tet(){
        d ? fetch(`https://maps.googleapis.com/maps/api/geocode/json?latlng=${d.coords.latitude},${d.coords.longitude}&key=AIzaSyANkyLK-kIz5HS56RyfJ9A6lleXQ3Lixdk`).then(response => {
            if (response.status >= 200 && response.status < 400) {
                return response.json()
            }
        }).then(res => this.setState({
            pl : res.results[0].formatted_address
    })) : console.log('no');
    }
    newLoc(e){
        let r = e.target.value;
        this.setState({
            pl: r
        })
    }
    validate(email) {
        let reg =  /^\w+@\w+\.\w{2,4}$/i;
        let address = email.target.value;
        if(reg.test(address) === false) {

            this.setState({
                ok: false
            });
        }
        else this.setState({
            ok: true
        });
    }
    render(){
        d = store.getState().pl;
        console.log(d);
        console.log(this.state.pl)
        return(
            <Provider store={store}>
            <div className="mainPart">
            <Ingridients />
                <div className='infoPart' >
                    <h1>Welcome to Pizza Creator!</h1>

                    <div onClick={count1}>
                    <Recepies />
                    <Tabs />
                    </div>
                    <div className='totalCount' >
                        <span>Total count: </span>
                    <p>{store.getState().sum}$</p>
                    </div>
                    <div className='registration' >
                        <div>
                            <label>Name</label>
                            <input type='text'/>
                        </div>
                        <br/>
                        <div>
                            <label>Email</label>
                            <input type='text' onChange={this.validate} className={this.state.ok ? 'ok' : 'again'}/>
                        </div>
                        <br/>
                    </div>
                    <div>
                        <p >You location is:</p>
                        <input type='text' id='lon' value={this.state.pl} onClick={this.tet} onChange={this.newLoc}/>
                    </div>
                </div>
                <Geolocation
                    onSuccess={console.log}
                    render={({
                                 fetchingPosition,
                                 position: { coords: { latitude, longitude } = {} } = {},
                                 error,
                                 getCurrentPosition,

                             }) =>
                        <div className='notDisplay'>
                            {latitude}
                            {longitude}
                        </div>}
                />
            </div>
            </Provider>
        )
    }
}

const render = () => {
    ReactDOM.render(<App/>, document.getElementById('root'));
};
store.subscribe(render);
render();



